@import url("https://fonts.googleapis.com/css2?family=Raleway:wght@400;800&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Open+Sans:wght@600&display=swap");
*,
*::before,
*::after {
	margin: 0;
	padding: 0;
	border: 0;
	box-sizing: border-box;
}
html,
body {
	background-color: #fff;
	color: #222;
	font-family: "Raleway", sans-serif;
	font-weight: 400;
	font-size: 62.5%;
	line-height: 1.7;
	text-rendering: optimizeLegibility;
	overflow-x: hidden;
}
input:focus,
select:focus,
textarea:focus,
button:focus {
	outline: none;
}
button {
	border: none;
	margin: 0;
	padding: 0;
	width: auto;
	overflow: visible;
	text-align: inherit;
	border-radius: 0;
	background: transparent;
	color: inherit;
	font: inherit;
	line-height: normal;
	-webkit-font-smoothing: inherit;
	-moz-osx-font-smoothing: inherit;
	-webkit-appearance: none;
}
li {
	list-style: none;
}
a {
	color: inherit;
	text-decoration: none;
}
button::-moz-focus-inner {
	border: 0;
	padding: 0;
}
.flex {
	display: flex;
	&.between {
		justify-content: space-between;
	}
	&.center {
		align-items: center;
	}
}
.container {
	max-width: 80%;
	margin: 0 auto;
}
nav {
	font-size: 2.8rem;
	padding: 2rem 0;
	width: 100%;
	position: absolute;
	i {
		color: #fff;
	}
	img {
		width: 100%;
	}
	z-index: 2;
}
.hero {
	background: url(../images/hero.png) no-repeat;
	background-position: center;
	background-size: cover;
	padding-top: 0rem;
	// padding-bottom: 14rem;
	height: 100vh;
	position: relative;
	z-index: 1;
	display: flex;
	flex-direction: column;
	align-items: center;
	justify-content: center;
	&::after {
		content: "";
		background-color: #0000006c;
		position: absolute;
		top: 0;
		left: 0;
		width: 100%;
		height: 100%;
		z-index: -1;
	}
	p {
		color: #fff;
		text-align: center;
		font-size: 2rem;
		text-transform: uppercase;
		margin: 2rem 0;
	}
	button {
		margin-top: 2rem;
	}
}
h1 {
	font-size: 4rem;
	color: #fff;
	text-transform: uppercase;
	line-height: 45px;
	text-align: center;
	letter-spacing: 8px;
	width: 80%;
	margin: 0 auto;
}
.input {
	display: flex;
	flex-direction: column;
}
input {
	font-size: 1.3rem;
	padding: 1.3rem 2rem;
	font-family: "Raleway", sans-serif;
}
button {
	color: #fff;
	background-color: #e7434e;
	display: flex;
	align-items: center;
	justify-content: center;
	font-size: 1.6rem;
	text-align: center;
	padding: 1.5rem 2rem;
	img {
		margin-right: 1rem;
	}
}
section {
	padding: 4rem 0;
}
#explore {
	background-color: #fff;
	p {
		font-size: 1.5rem;
		margin: 2rem 0;
		text-align: center;
		color: gray;
	}
	.images-showcase {
		display: flex;
		flex-direction: column;
		align-items: center;
		justify-content: center;
		color: #fff;
		.content {
			margin-top: 5rem;
			margin-left: 1.5rem;
		}
		.city {
			font-size: 2.2rem !important;
			text-align: left !important;
			margin: 0 !important;
			color: #fff !important;
		}
		.country {
			font-size: 1.5rem !important;
			text-align: left !important;
			margin: 0 !important;
			color: #fff !important;
		}
		.image {
			width: 100%;
			height: 20rem;
			background-size: cover;
			&:not(:last-of-type) {
				margin-bottom: 2rem;
			}
			&:nth-of-type(1) {
				background: linear-gradient(
						to bottom,
						rgba(0, 0, 0, 0) 0%,
						rgba(0, 0, 0, 0) 0%,
						rgba(0, 0, 0, 0) 0%,
						rgba(0, 0, 0, 0) 0%,
						rgba(0, 0, 0, 0.8) 100%
					),
					url(../images/explore1.png) no-repeat;
				background-position: center;
			}
			&:nth-of-type(2) {
				background: linear-gradient(
						to bottom,
						rgba(0, 0, 0, 0) 0%,
						rgba(0, 0, 0, 0) 0%,
						rgba(0, 0, 0, 0) 0%,
						rgba(0, 0, 0, 0) 0%,
						rgba(0, 0, 0, 0.8) 100%
					),
					url(../images/explore2.png) no-repeat;
				background-position: center;
			}
			&:nth-of-type(3) {
				background: linear-gradient(
						to bottom,
						rgba(0, 0, 0, 0) 0%,
						rgba(0, 0, 0, 0) 0%,
						rgba(0, 0, 0, 0) 0%,
						rgba(0, 0, 0, 0) 0%,
						rgba(0, 0, 0, 0.8) 100%
					),
					url(../images/explore3.png) no-repeat;
				background-position: center;
			}
			&:nth-of-type(4) {
				background: linear-gradient(
						to bottom,
						rgba(0, 0, 0, 0) 0%,
						rgba(0, 0, 0, 0) 0%,
						rgba(0, 0, 0, 0) 0%,
						rgba(0, 0, 0, 0) 0%,
						rgba(0, 0, 0, 0.8) 100%
					),
					url(../images/explore4.png) no-repeat;
				background-position: center;
			}
			&:nth-of-type(5) {
				background: linear-gradient(
						to bottom,
						rgba(0, 0, 0, 0) 0%,
						rgba(0, 0, 0, 0) 0%,
						rgba(0, 0, 0, 0) 0%,
						rgba(0, 0, 0, 0) 0%,
						rgba(0, 0, 0, 0.8) 100%
					),
					url(../images/explore5.png) no-repeat;
				background-position: center;
			}
		}
		.boats {
			background-color: #e7434e;
			color: #fff;

			align-items: center;
			justify-content: center;
			display: inline-flex;
			width: 7rem;
			height: 4rem;
			border-radius: 4px;
			margin: 1rem;
			img {
				margin-right: 0.5rem;
			}
			.number {
				font-weight: bolder;
				font-family: "Open Sans", sans-serif;
				color: #fff !important;
			}
		}
	}
}
h2 {
	font-size: 3rem;
	line-height: 30px;
	text-transform: uppercase;
	text-align: center;
}
aside {
	background-color: #fff;
	box-shadow: 0px 7px 5px 0px rgba(0, 0, 0, 0.43);
	font-size: 2rem;
	text-align: center;
	padding: 2rem 0;
	transform: translateY(-50%);
	position: relative;
	z-index: 5;
	.number {
		font-weight: bolder;
		font-family: "Open Sans", sans-serif;
	}
	p {
		font-size: 1.4rem;
		text-transform: uppercase;
	}
	.features {
		display: flex;
		align-items: center;
		justify-content: space-between;
	}
}

.slick-next,
.slick-prev {
	display: none !important;
}
.btn {
	margin-top: 4rem;
	font-size: 1.4rem;
	color: #222;
	background-color: #fff;
	font-weight: bolder;
	margin-left: auto;
	margin-right: auto;
	border: 1px solid rgba(34, 34, 34, 0.397);
}
#haveboat {
	background: url(../images/hero2.png) no-repeat;
	background-size: cover;
	background-position: center;
	p {
		font-size: 2.2rem;
		color: #fff;
		text-align: center;
		margin: 2rem auto;
		width: 60%;
	}
	button {
		font-size: 1.4rem;
		margin: 2rem auto;
	}
	position: relative;
	z-index: 1;
	&::after {
		content: "";
		background-color: #0000006c;
		position: absolute;
		top: 0;
		left: 0;
		width: 100%;
		height: 100%;
		z-index: -1;
	}
}
h2 {
	font-weight: lighter;
}
